//import OSettings from "@/components/organisms/OSettings";

export default {
  name: "o-sidebar",
  // components: { OSettings },

  props: {
    theme: {
      type: String,
      default() {
        return "light";
      }
    }
  },
  computed: {
    compTheme() {
      if (this.theme === "light") {
        return ["bg-light", "text-dark"].join(" ");
      } else {
        return ["bg-dark", "text-light"].join(" ");
      }
    }
  }
};